import React from 'react'
import gridStyle from '@components/atoms/Grid/styles.module.css'
import style from './styles.module.css'
import clsx from 'clsx'
import { ComponentBuildingBlockMasonry, Enum_Page_Color } from '@generated/graphql-request'
import Image from 'next/image'
import { getWebsiteApiOrigin } from '@misc/environments'

export default function Masonry (props: ComponentBuildingBlockMasonry & { color: Enum_Page_Color}): JSX.Element {
  return (
    <div className={clsx(gridStyle.grid, style.masonry)}>
      <div className={style.grid}>
        <div className={clsx(
          style.bgBlock,
          props.color === 'pink' && style.bgBlockPink,
          props.color === 'green' && style.bgBlockGreen,
          props.color === 'red' && style.bgBlockRed,
          props.color === 'blue' && style.bgBlockBlue
        )}
        >
          <div className={style.bgBlockWrapper} />
        </div>
        <div className={style.leftPhoto}>
          <div className={style.leftPhotoWrapper}>
            <Image src={`${getWebsiteApiOrigin()}{props.photoLeft?.data?.attributes?.url ?? ''}`} alt={props.photoLeft?.data?.attributes?.caption ?? ''} layout='fill' objectFit='cover' objectPosition='center center' />
          </div>
        </div>
        <div className={style.rightCol}>
          <div className={style.rightTopPhoto}>
            <div className={style.rightTopPhotoWrapper}>
              <Image src={`${getWebsiteApiOrigin()}{props.photoRightTop?.data?.attributes?.url ?? ''}`} alt={props.photoRightTop?.data?.attributes?.caption ?? ''} layout='fill' objectFit='cover' objectPosition='center center' />
            </div>
          </div>
          <div className={style.rightBottomPhoto}>
            <div className={style.rightBottomPhotoWrapper}>
              <Image src={`${getWebsiteApiOrigin()}{props.photoRightBottom?.data?.attributes?.url ?? ''}`} alt={props.photoRightBottom?.data?.attributes?.caption ?? ''} layout='fill' objectFit='cover' objectPosition='center center' />
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
